import React, { useEffect, useState } from 'react'
import { Button, Container, Grid, Paper, Skeleton, Table, TableHead, } from '@mui/material'
import axios from 'axios'
import Row from '../Components/Row'
import InterviewRow from '../Components/InterviewRow'
/* import { DataGrid } from '@mui/x-data-grid'
import { useDemoData } from '@mui/x-data-grid-generator'
import { GridToolbar } from '@mui/x-data-grid' */
import { CSVLink } from 'react-csv'



  
export default function Home() {

    const [students, setStudents] = useState([])
    const [interviews, setInterviews] = useState([])
    const [refresh, setRefresh] = useState(false)
    const [comLoading, setComLoading] = useState(false)
    const [report, setReport] = useState()

    // const newDate = new Date()

    // data grid for downloading CSV file

    const headers = [
        { label: 'student Id', key: '_id' },
        { label: 'Student name', key: 'name',},
        { label: 'Student email', key: 'email',},
        { label: 'Student College', key: 'college',  },
        { label: 'Student Status', key: 'status',  },
        { label: 'DSA Final Score', key: 'dsaScore',  },
        { label: 'Web Develeopemnt Final Score', key: 'webDScore',  },
        { label: 'React Final Score', key: 'reactScore',  },
        { label: 'Interview company', key: 'interviews.company',  },
        { label: 'Interview date', key: 'interviews.date',  },
        { label: 'Interview result', key: 'interviews.result',},
    ]

    const csvReport = {
        data: students,
        headers: headers,
        filename: `Student Report ${Date()}`
    }

    /* const headers = [
        {label: 'id', key: 'students._id'},
        {label: 'name', key: 'students.name'},
        {label: 'email', key: 'students.email'},
    ]

    const data = [
        students
    ] */

    

    // console.log("csvRerport: ", csvReport);
    // console.log("csvRerport: ", csvData);
    


    const getAllStudents = async() =>{
        return await axios.get(`http://localhost:4000/student/getStudent`).then((response) => {
            setStudents(response.data.result)
            setComLoading(true)
        }).catch((err) => {
            console.log("error: on getAllStudents", err);
            console.log(true);
        })
    }

    const getAllCompanys = async() => {
        return await axios.get(`http://localhost:4000/interview/getAllInterviews`).then((response) => {
            setInterviews(response.data.result)
            setComLoading(true)
        }).catch((err) => {
            console.log("error on getAllCompanys: ", err);
            setComLoading(true)
        })
    }

    const getAllReports = async() => {
        return axios.get(`http://localhost:4000/report/getAllReport`).then((response) => {
            setReport(response.result)
            console.log("response: ", response);
            if(refresh){
                setRefresh(false)
            }else{
                setRefresh(true)
            }
        }).catch((err) => {
            console.log("error: ", err);
        })
    }

    useEffect(() => {
        getAllStudents()
        getAllCompanys()
    }, [refresh])
    // console.log("comapny: ", interviews);
    // console.log("student: ", students);
    
    return (
        <Container>
            <Grid container spacing={2}>
                
                {/* <DataGrid getRowId={(row) => row.statId}/> */}
                <Grid item xs={12} sm={12} md={6} lg={6} >
                    {comLoading ? (
                        <Table component={Paper} sx={{ mt: 2, width: '100%', padding: '10px'}}>
                            <TableHead >
                                {students.map((item, ind) => {
                                    return <Row key={ind} student={item} studentId={item._id} refresh={refresh} setRefresh={setRefresh} />
                                })}
                            </TableHead>
                        </Table>
                    ) : <>
                        <Skeleton width={"100%"} height={"90px"} animation="wave" />
                    </>}
                </Grid>
                <Grid item xs={12} sm={12} md={6} lg={6} >
                    {comLoading ? (
                        <Table component={Paper} sx={{ mt: 2, width: '100%', padding: '10px'}}>
                            <TableHead >
                                {interviews.map((item, ind) => {
                                    return <InterviewRow key={ind} company={item} companyId={item._id} refresh={refresh} setRefresh={setRefresh} />
                                })}
                            </TableHead>
                        </Table>
                    ) : <>
                        <Skeleton width={"100%"} height={"90px"} animation="wave" />
                    </>}
                </Grid>
                <Grid item xs={12} >
                    
                    <CSVLink data={students} headers={headers} filename={`Student Report ${Date()}`} /* data={JSON.stringify(csvReport)}  */>Download Me</CSVLink>
                    {/* <CSVLink data={csvData} headers={headers} filename={`Student Report ${Date()}`}  >Export to CSV</CSVLink> */}
                </Grid>
            </Grid>
            
            
        </Container>
    )
}